.document-content-placeholder {
    position: absolute;
    margin-top: 25%;
    margin-left: 5%;
    margin-bottom: 0;
    color: #7e7e7e;
    user-select: none;
    pointer-events: none;
    cursor: default;
}

.document-content-bg {
    outline: none;
    padding-top: 25%;
    padding-left: 5%;
    height: 100%;
    font-size: 1.25rem;
}

.document-content-full-height {
    height: 100%;
    position: relative;
}
