.dc-bg {
    padding-top: 35%;
    height: 100%;
}

.dc-placeholder-container {
    font-size: 1rem;
    height: 100%;
    position: relative;
    max-width: 320px;
    margin: auto;
}

.dc-placeholder-img {
    display: block;
    margin: auto;
    margin-bottom: 12px;
    width: 128px;
}

.dc-placeholder-title {
    text-align: center;
    font-weight: 700;
    margin-bottom: 8px;
    font-size: 1.25rem;
}

.dc-placeholder-ref {
    margin-top: 8px;
    font-size: 0.75rem;
    position: absolute;
    bottom: 0;
}

.card-bg {
    background-color: #1e1e1e;
    margin: 16px 0;
    padding: 16px 32px;
    border-radius: 4px;
}

.card-description {
    font-size: 0.9rem;
    margin: 12px 0;
}

.card-original-word {
    text-decoration: line-through;
    color: #ef9a9a;
}

.card-original-word > span {
    color: #e2e2e2;
}

.card-arrow {
    margin: 0 12px;
}

.card-suggestion {
    background-color: #a5d6a7;
    padding: 2px 6px;
    color: #121212;
    border-radius: 2px;
    cursor: pointer;
}

.card-add-dictionary-icon {
    margin-right: 8px;
    font-size: 1rem;
}

.card-add-dictionary {
    cursor: pointer;
    font-size: 1rem;
}

.card-discard {
    cursor: pointer;
    float: right;
    font-size: 1rem;
}
